/* checkbox */
.pwa-checkbox {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: text-bottom;
}

.pwa-checkbox-inner {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 14px;
    height: 14px;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    background-color: #fff;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.pwa-checkbox-inner:after {
    -webkit-transform: rotate(45deg) scale(0);
        -ms-transform: rotate(45deg) scale(0);
            transform: rotate(45deg) scale(0);
    position: absolute;
    left: 4px;
    top: 1px;
    display: table;
    width: 5px;
    height: 8px;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    content: ' ';
    -webkit-transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    -o-transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
}

input[type="checkbox"] {
    position: absolute;
    left: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    /* filter: alpha(opacity=0); */
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

.pwa-checkbox-checked .pwa-checkbox-inner:after {
    -webkit-transform: rotate(45deg) scale(1);
        -ms-transform: rotate(45deg) scale(1);
            transform: rotate(45deg) scale(1);
    position: absolute;
    left: 4px;
    top: 1px;
    display: table;
    width: 5px;
    height: 8px;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    content: ' ';
    -webkit-transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
    -o-transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
    transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
}

.pwa-checkbox-checked .pwa-checkbox-inner {
    background-color: #6666FF;
    border-color: #999999;
}

.pwa-checkbox-disabled.pwa-checkbox-checked .pwa-checkbox-inner:after {
    -webkit-animation-name: none;
            animation-name: none;
    border-color: rgba(0, 0, 0, 0.25);
}

.pwa-checkbox-disabled .pwa-checkbox-inner {
    border-color: #d9d9d9 !important;
    background-color: #f3f3f3;
}

.pwa-checkbox-disabled .pwa-checkbox-inner:after {
    -webkit-animation-name: none;
            animation-name: none;
    border-color: #f3f3f3;
}

.pwa-checkbox-disabled +span {
    color: #6666FF;
    cursor: not-allowed;
}

.pwa-checkbox-wrapper {
    cursor: pointer;
    font-size: 12px;
    display: inline-block;
}

.pwa-checkbox-wrapper+span,
.pwa-checkbox+span {
    padding-left: 8px;
    padding-right: 8px;
    color: #6666FF;
}

.pwa-checkbox-checked input[type="checkbox"]:checked+.pwa-checkbox-inner {
    border-color: #6666FF;
    background-color: #6666FF;
}

.pwa-checkbox-checked .pwa-checkbox-inner {
    background-color: #999999;
}

